// __tests__/navigation/AuthStack.test.tsx
import React from 'react';
import renderer from 'react-test-renderer';
import AuthStack from '../../src/navigation/AuthStack';

// 네비게이션 모킹
jest.mock('@react-navigation/native-stack', () => ({
  createNativeStackNavigator: () => ({
    Navigator: ({ children, screenOptions }) => children,
    Screen: ({ name }) => name,
  }),
}));

// 화면 컴포넌트 모킹
jest.mock('../../src/screens/LoginScreen', () => 'LoginScreen');
jest.mock('../../src/screens/RegisterScreen', () => 'RegisterScreen');
jest.mock('../../src/screens/ApiTestScreen', () => 'ApiTestScreen');

describe('AuthStack', () => {
  it('렌더링 오류 없이 생성됨', () => {
    const tree = renderer.create(<AuthStack />).toJSON();
    expect(tree).toBeDefined();
  });

  it('스냅샷과 일치함', () => {
    const tree = renderer.create(<AuthStack />).toJSON();
    expect(tree).toMatchSnapshot();
  });
});